import { SchedularService } from './../dashboard/schedular/schedular.service';
import { DataService } from './../data/data.service';
import { NotebookService } from './notebook.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { NotebookDialogComponent } from './notebook-dialog/notebook-dialog.component';

const swal = require('sweetalert');
@Component({
  selector: 'app-notebook',
  templateUrl: './notebook.component.html',
  styleUrls: ['./notebook.component.scss'],
})
export class NotebookComponent implements OnInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild('notebookPaginator') set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild('notebookSort') set sort(value: MatSort) {
    this.dataSource.sort = value;
  }

  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showSpinner: boolean = false;
  user: any;
  project: any;
  notebooksArray: Array<any>;
  dataGroupList: Array<any>;
  dataSetList: Array<any>;
  safeUrl: SafeResourceUrl;
  showOverview: boolean = false;
  notebookName: string;
  constructor(
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dialog: MatDialog,
    private _sanitizer: DomSanitizer,
    private _notebook: NotebookService,
    private _data: DataService,
    private _window: Window,
    private _schedular: SchedularService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.displayedColumns = ['srNo', 'notebookName', 'createdAt', 'actions'];
  }

  /**
   * Initialize all the required setting/data
   */
  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getNotebooksList();
    this.getDataGroupList();
    this.getDataSetList();
  }

  /**
   * Fetches all the created notebook from DB
   */
  getNotebooksList(): void {
    this._notebook
      .getAllNotebooks(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this._spinner.stopSpinner();
          if (res) {
            this.notebooksArray = res;
            this.dataSource.data = res;
            this.dataSource.paginator = this.paginator;
            console.log('ðŸš€ ~ this.sort', this.sort);
            this.dataSource.sort = this.sort;
            this.dataSource._updateChangeSubscription();
          } else {
            this._toaster.error('Cannot fetch notebooks', 'Notebooks');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Notebooks');
        }
      );
  }

  /**
   * Fetches all the data groups list
   */
  getDataGroupList(): void {
    this._data
      .getDataGroupsList(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.dataGroupList = res;
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Flows');
        }
      );
  }

  /**
   * Fetches the data set list
   */
  getDataSetList(): void {
    this._data
      .getAllProjectDatas(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.dataSetList = res[0].filesList;
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Set');
        }
      );
  }

  /**
   * Handles the delete operation to delete notebook
   * @param {connection id} id
   * @param {connection type} type
   */
  deleteHandler(notebookData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this notebook!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.showSpinner = true;
        this._notebook
          .deleteNotebook(this.project._id, notebookData._id, notebookData.fileName)
          .subscribe(
            (res: any) => {
              if (res) {
                this.notebooksArray = this.notebooksArray.filter(
                  (item) => item._id !== notebookData._id
                );
                this.dataSource.data = this.notebooksArray;
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
                this.dataSource._updateChangeSubscription();
                this._toaster.success(`${notebookData.fileName} deleted successfully`, 'Notebooks');
              } else {
                this._toaster.error('Cannot delete the notebook', 'Notebooks');
              }
              this.showSpinner = false;
            },
            (err: Error) => {
              this.showSpinner = false;
              this._toaster.error('Server Error', 'Notebooks');
            }
          );
      }
    });
  }

  /**
   * Opens the dialog with form
   */
  openFormDialog(): void {
    let dialodRef = this._dialog.open(NotebookDialogComponent, {
      minWidth: '50vw',
      data: {
        dataGroups: this.dataGroupList,
        dataSets: this.dataSetList,
      },
    });

    dialodRef.afterClosed().subscribe((data) => {
      if (data) {
        this.getNotebooksList();
      }
    });
  }

  /**
   * Setups the notebook in service
   * @param {*} notebookData notebook details
   */
  openNotebook(notebookData: any) {
    this.notebookName = notebookData.fileName;
    this._spinner.showSpinner();
    let url: string = `${notebookData.url}${notebookData.fileName}?uid=${this.project.createdBy}&pid=${this.project._id}`;
    this.safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(url);
    this.showOverview = true;
    this._spinner.stopSpinner();
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
