import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class NotebookService {

  serverBaseUrl = environment.serverBaseUrl;
  formData: FormData = new FormData();

  constructor(private _http: HttpClient) { }

  /**
   * Fetches all the notebooks from DB
   */
  getAllNotebooks(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/projects/${projectId}/notebooks`);
  }

  /**
   * Deletes the notebook from DB
   * @param projectId 
   * @param notebookId 
   * @param notebookName 
   * @returns 
   */
  deleteNotebook(projectId: string, notebookId: string, notebookName: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/projects/${projectId}/notebook/${notebookId}/delete/${notebookName}`);
  }

  /**
   * Creates a new notebook
   * @param projectId 
   * @param notebookData 
   * @returns 
   */
  createNewNotebook(projectId: string, notebookData: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/projects/${projectId}/notebook/save`, notebookData);
  }
}
