import { Pipe, PipeTransform } from '@angular/core';
/**
 * Removes the extesion from a filename
 * Accepts file name as a parameter
 * Usage:
 *  value|dropFileExtension
 * Example:
 *  {{ 'example.txt' | dropFileExtension: fileSource }}
 *  formats to example
 */
@Pipe({ name: 'dropFileExtension' })
export class DropFileExtensionPipe implements PipeTransform {
  transform(value: string, fileSource: string) {
    return fileSource && value ? value.substr(0, value.lastIndexOf('.')) : value;
  }
}
