import { NotebookService } from './../notebook.service';
import { NotebookComponent } from './../notebook.component';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { DataService } from '../../data/data.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-notebook-dialog',
  templateUrl: './notebook-dialog.component.html',
  styleUrls: ['./notebook-dialog.component.scss'],
})
export class NotebookDialogComponent implements OnInit {
  /** Form group instance */
  notebookForm: FormGroup;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showSpinner: boolean = false;
  addToPipeline: boolean = false;
  project: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _data: DataService,
    private _toaster: ToastrService,
    private _notebookDialogRef: MatDialogRef<NotebookComponent>,
    private _localStorage: LocalStorageService,
    private _notebook: NotebookService
  ) {
    /** Form instance */
    this.notebookForm = this._fb.group({
      fileName: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_ ]+$')]],
      inputFiles: ['', Validators.nullValidator],
      outputFileDataGroupId: ['', Validators.nullValidator],
    });
  }

  ngOnInit(): void {
    this.project = this._localStorage.getItem('project');
  }

  /**
   * Creates a new notebook
   * @param formValue
   */
  onSubmitNotebook(formValue: any): void {
    this.showSpinner = true;
    let reqObj: any = {
      projectPath: '',
      fileName: `${formValue.fileName}-${Date.now()}.ipynb`,
      inputFile: formValue.inputFiles.name,
      notebookInputFileId: formValue.inputFiles._id,
      outputFileDataGroupId: formValue.outputFileDataGroupId,
      addToPipeline: this.addToPipeline,
      projectId: this.project._id,
      createdBy: this.project.createdBy,
    };

    this._notebook
      .createNewNotebook(this.project._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.showSpinner = false;
          if (res) {
            this._toaster.success('Notebook created successfully', 'Notebooks');
            this.closeModal(res);
          } else {
            this._toaster.error('Cannot create new notebook', 'Notebooks');
          }
        },
        (err: Error) => {
          this.showSpinner = false;
          this._toaster.error('Server Error', 'Notebooks');
        }
      );
  }

  /**
   * Closes the opened modal by sending the data or without data
   */
  closeModal(data?: any): void {
    this._notebookDialogRef.close(data);
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
